.tagNode .anchor {
  fill: lightgray;
  stroke: green;
  stroke-width: 0.5px; }
  .tagNode .anchor.fixed {
    fill: red; }

.tagNode .tag .frame {
  fill: #707070; }

.tagNode .tag .bg {
  fill: #f0f0f0; }

.tagNode .tag text {
  font-size: 1.5em;
  font-weight: normal;
  stroke: none;
  -webkit-user-select: none;
  /* Chrome all / Safari all */
  -moz-user-select: none;
  /* Firefox all */
  -ms-user-select: none;
  user-select: none; }
  .tagNode .tag text.excluded {
    text-decoration: line-through; }

.tagNode .tag.highlighted .bg {
  fill: yellow; }

.cg .overlay {
  fill: none;
  pointer-events: all; }

.cg .link {
  fill: none;
  stroke: #ddd;
  stroke-width: 0.5px;
  opacity: 1; }
  .cg .link.highlight {
    stroke: red;
    stroke-opacity: 1; }
